.Pagination-container {
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    .page-item {
        width: 25px;
        height: 10px;
        color: #3483fA;
        background-color: #EEE;
        cursor: pointer;

        &.active {
            color: rgb(37, 179, 223);
        }
    }
}